table {
  width: 800px;
  border-collapse: collapse;
  font-size: 14px;
  line-height: 24px;
  font-family: "Arial", sans-serif;
  color: #212121; }

table tr td, table tr th {
  padding: 10px 15px; }


table table {
  width: 100%; }


/*TOP*/
table tr:nth-child(2) td {
  vertical-align: top;
  padding: 0; }

table tr:nth-child(2) td table tr td {
  padding: 5px 15px; }

table tr:nth-child(2) td table:nth-child(1) tr td:nth-child(1) {
  width: 30px; }

table tr:nth-child(2) td table:nth-child(2) {
  border-top: 2px solid #5865C1; }
table tr:nth-child(2) td:nth-child(2) table {
  border: 2px solid #5865C1; }


/*table*/
table tr:nth-child(3) td {
  vertical-align: top;
  padding: 0; }
table tr:nth-child(3) td table {
  margin: 20px 0 0 0; }
table tr:nth-child(3) td table thead tr th {
  ackground-color: #5865C1;
  font-weight: 500;
  color: #ffffff; }

table tr:nth-child(3) td table tbody tr td {
  padding: 5px 10px;
  border: 1px solid #E2E2E2; }

table tr:nth-child(3) td table tfoot tr td {
  padding: 5px 10px;
  border: 1px solid #E2E2E2; }



/*sign*/
table tr:nth-child(5) td {
  padding-left: 500px;
  padding-top: 40px; }

table tr:nth-child(5) td p span {
  display: block;
  padding-top: 60px; }
